* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    color: #1A1817;
    background-color: #fff;
    background: linear-gradient(45deg, #df01f873, #2761a3b4);
    min-height: 100vh;
  }
  
  #root {
    min-height: 100vh;
    max-width: 700px;
    margin: 0 auto;
  }
  
  h1 {
    color: #505050;
  }
  
  .tweet {
    display: flex;
    width: 100%;
    border-radius: 4px;
    color: #505050;
    padding: 12px;
    margin-bottom: 16px;
    box-shadow: 20px 20px 40px -6px rgba(0,0,0,0.2);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-right-color: rgba(255, 255, 255, 0.2);
    border-bottom-color: rgba(255, 255, 255, 0.2);
    background-color: rgba(255, 255, 255, 0.587);
    -webkit-backdrop-filter: blur(20px);
    backdrop-filter: blur(20px);
  }
  
  .icon-container {
    width: 1.5em;
    font-size: 3em;
    padding: 0.2em
  }
  
  .body-container {
    flex: 1;
    padding: 0.5em;
  }
  
  .account-name {
    color: #868686
  }
  
  .status-icon {
    color: #ff6e6e;
  }
  
  .status-icon span {
    cursor: pointer;
  }
  
  .tweet-textarea {
    font-size: 1em;
    width: 100%;
    height: 5em;
    padding: 0.8em;
    resize: vertical;
    margin-top: 16px;
    border: 2px solid #fff;
    border-radius: 4px;
    transition: all 0.2s ease-in-out;
  }
  
  .send-tweet {
    font-size: 1em;
    padding: 0.5em 1em;
    border: 2px solid #014587;
    border-radius: 4px;
    background-color: #014587; 
    color: white;
    box-shadow: 0 5px #002e5c;
    cursor: pointer;
  }
  
  .send-tweet:active {
    box-shadow: 0 5px #666;
    transform: translateY(4px);
  }
  
  .timeline,
  .tweet-input {
    padding: 24px 60px;
  }
  
  @media screen and (max-width: 700px) {
    .timeline,
    .tweet-input {
        padding: 8px 16px;
    }
  
    .tweet-input {
        margin-bottom: 16px;
    }
  }